.App {
  text-align: center;
  background-color:rgb(161, 161, 255)
}

.App-logo {
  height: 40vmin;
}

.App-logo {
  height: 35vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.title {
  background-color: rgb(103, 103, 219);
  text-align: center;
  align-self: center;
}

.h1 {
  color: black;
  text-align: center;
  align-content: center;
}


.board {
  align-items: center;
  padding: 150px;
  width: 300px;
  margin: auto;
  justify-content: center;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  box-shadow: #61dafb;
}

.card-single {
  background-color: rgb(159, 45, 123);
  background-image: "back.jpg";
  margin: 3px;
  width: 160px;
  height: 160px;
  box-shadow: #4c4d4d;
  border-radius: 10px;
}
.images {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.images img {
  min-width: 100px;
  height: auto;
}

.card {
  height: auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  place-items: center;
  justify-content: center;

}

.cardgrid {
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.card {
  /* height: 100px;
  width: 100px; */

  /* object-fit: contain; */
}

.front {
  object-fit: cover;
  height: 200px;
  width: 200px;
}

.back {
  object-fit: cover;
  height: 200px;
  width: 200px;
}